#background {
    position: fixed;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.page-body {
    margin: auto;
    width: 80%;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.box-container {
    background-color: #E5E9EA;
    box-shadow: 0.5rem 0.5rem 0.5rem 0rem #C3C3C3;
}